import React, { Component } from 'react';
import { Row, Col, Card, CardBody } from 'reactstrap';
import { BaseUrl } from "../../helpers/Baseurl"
//Import Charts
import ReactApexChart from 'react-apexcharts';
import "./dashboard.scss";

class RevenueAnalytics extends Component {
    constructor(props) {
        super(props);
        this.state = {
            this_month : 0.0,
            pre_month: 0.0,
            prepre_month:0.0,
            series: [{
                name: 'By Sold Jobs',
                data: [0, 0, 0 ]
            }],
            month_name:['Jan', "Feb", "Mar"]
            
        };
    }

    componentDidMount () {
        this.loadData()
    }
    async loadData(){

        const requestOptions = {
          method: 'GET',
          headers: { 'Content-Type': 'application/json' },
        };
        await fetch(`${BaseUrl}/api/jobs/revenue_analysis/`,requestOptions)
            .then(res => res.json())
            .then(
                (data) => {
                    console.log(data, '-data')
                    let series= [{
                        name: 'By Sold Jobs',
                        data: data.months
                    }]
                //    const {options} = this.state
                //     let opti = options
                //     opti["xaxis"]["categories"] = data.month_name
                //     console.log(opti["xaxis"]["categories"], '---------------')
                //     // months = data.month_name
                    
                    this.setState({ 
                        this_month:data.this_month,
                        pre_month: data.pre_month,
                        prepre_month: data.prepre_month,
                        series:series,
                        month_name:data.month_name,
                    })           
                   
                },
                (error) => {
                    console.log(error, 'error--------------') 
                }
            )  
       }


    render() {
       let options = {
            chart: {
                toolbar: {
                    show: false,
                }
            },
            plotOptions: {
                bar: {
                    horizontal: false,
                    columnWidth: '45%',
                    endingShape: 'rounded'	
                },
            },
            dataLabels: {
                enabled: false
            },
            stroke: {
                show: true,
                width: 2,
                colors: ['transparent']
            },
            
            colors: ['#34c38f'],
            xaxis: {
                categories: this.state.month_name,
            },
            yaxis: {
                title: {
                    text: '(Actual Selling Price)'
                }
            },
            grid: {
                borderColor: '#f1f1f1',
            },
            fill: {
                opacity: 1
        
            },
            tooltip: {
                y: {
                    formatter: function (val) {
                        return "$"+ val  }
                }
            }
        }
        return (
            <React.Fragment>
                <Card>
                    <CardBody>
                      
                        <h4 className="card-title mb-4">Revenue Analytics</h4>
                        <div>
                            <div id="line-column-chart" className="apex-charts" dir="ltr">
                            <ReactApexChart options={options} series={this.state.series} type="bar" height={350} />
                            </div>
                        </div>
                    </CardBody>

                    <CardBody className="border-top text-center">
                        <Row>
                            <Col sm={4}>
                            <div className="mt-4 mt-sm-0">
                                    <p className="mb-2 text-muted text-truncate"><i className="mdi mdi-circle text-success font-size-10 me-1"></i> 2nd Last Month :</p>
                                    <div className="d-inline-flex">
                                        <h5 className="mb-0 me-2">$ {this.state.prepre_month}</h5>
                                       
                                    </div>
                                </div>
                               
                            </Col>

                            <Col sm={4}>
                                <div className="mt-4 mt-sm-0">
                                    <p className="mb-2 text-muted text-truncate"><i className="mdi mdi-circle text-success font-size-10 me-1"></i> Last Month :</p>
                                    <div className="d-inline-flex">
                                        <h5 className="mb-0 me-2">$ {this.state.pre_month}</h5>
                                       
                                    </div>
                                </div>
                            </Col>
                            <Col sm={4}>
                            <div className="mt-4 mt-sm-0">
                                    <p className="mb-2 text-muted text-truncate"><i className="mdi mdi-circle text-success font-size-10 me-1"></i> This Month :</p>
                                    <div className="d-inline-flex">
                                        <h5 className="mb-0 me-2">$ {this.state.this_month}</h5>
                                       
                                    </div>
                                </div>
                               
                            </Col>

                        </Row>
                    </CardBody>
                </Card>
            </React.Fragment>
        );
    }
}

export default RevenueAnalytics;