import React, { Component } from 'react';
import { Card, CardBody, Col, Dropdown, DropdownToggle, DropdownMenu, DropdownItem, Table, Spinner } from "reactstrap";
import { BaseUrl } from "../../helpers/Baseurl"

class Sources extends Component {
    constructor(props) {
        super(props);
        this.state = {
            loading : true,
            menu: false,
            user_options:[],
            user:"",
            bidding:0,
            delevered:0,
            sold:0,
            lost:0,
            completed:0,

        }
    };
    componentDidMount() {
        this.setState({loading : true})
        this.loadData()
    }

    async loadData(){

 
        const requestOptions = {
               method: 'GET',
               headers: { 'Content-Type': 'application/json' },
           };
           await fetch(`${BaseUrl}/api/jobs/job_analtics/`,requestOptions)
               .then(res => res.json())
               .then(
                   (data) => {
                    let user = ""
                    if ((data.users).length > 0){
                        user = data.users[0].email
                    } 
                    this.setState({ 
                        loading : false,
                        user:user,
                        user_options:data.users,
                        bidding:data.bidding,
                        delevered:data.delivered,
                        sold:data.sold,
                        lost:data.lost,
                        completed:data.completed,
                    })    
                      
                   },
                   (error) => {
                       console.log(error, 'error--------------') 
                   }
               )  
     }

     async handleSoldJob(id, email){
        this.setState({loading : true})
        const requestOptions = {
            method: 'GET',
            headers: { 'Content-Type': 'application/json' },
          };
          await fetch(`${BaseUrl}/api/jobs/job_analtics/${id}`,requestOptions)
              .then(res => res.json())
              .then(
                  (data) => {
                    this.setState({ 
                        loading : false,
                        user:email,
                        user_options:data.users,
                        bidding:data.bidding,
                        delevered:data.delivered,
                        sold:data.sold,
                        lost:data.lost,
                        completed:data.completed,
                    })          
                     
                  },
                  (error) => {
                      console.log(error, 'error--------------') 
                  }
              )  
    }
    
    render() {
        const {user_options,bidding, delevered,sold,lost,completed, user  } = this.state 
        return (
            <React.Fragment>
                <Col lg={12}>
                    <Card>
                        <CardBody>
                            <Dropdown className="float-end" isOpen={this.state.menu} toggle={() => this.setState({ menu: !this.state.menu })} >
                                <DropdownToggle tag="i" className="arrow-none card-drop">
                                    <i className="mdi mdi-dots-vertical"></i>
                                </DropdownToggle>
                                <DropdownMenu className="dropdown-menu-end">

                                {user_options.map(comp => {
                                        return <DropdownItem  key={comp.id} onClick={() => this.handleSoldJob(comp.id, comp.email)}>{comp.email}</DropdownItem>
                                    })}
                                </DropdownMenu>
                            </Dropdown>

                            <h4 className="card-title mb-3">Jobs By User</h4>

                            <div>
                                <div className="text-center">
                                    <p className="mb-2">{user}</p>
                                  
                                </div>

                                {this.state.loading?(
                                    <Spinner className="me-2" style={{marginLeft: "45%",  marginTop: "5%"}} color="primary" />
                                ):(

                                <div className="table-responsive mt-4">
                                    <Table hover className=" mb-0 table-centered table-nowrap">
                                        <tbody>
                                            <tr>

                                                <td>
                                                    <h5 className="font-size-14 mb-0">Bidding</h5>
                                                </td>
                                                <td><div id="spak-chart1"></div></td>
                                                <td>
                                                    <p className="text-muted mb-0">{bidding}</p>
                                                </td>
                                            </tr>
                                            <tr>
                                                
                                                <td>
                                                    <h5 className="font-size-14 mb-0">Delivered</h5>
                                                </td>

                                                <td><div id="spak-chart2"></div></td>
                                                <td>
                                                    <p className="text-muted mb-0">{delevered}</p>
                                                </td>
                                            </tr>
                                            <tr>
                                                <td>
                                                    <h5 className="font-size-14 mb-0">Sold</h5>
                                                </td>
                                                <td><div id="spak-chart3"></div></td>
                                                <td>
                                                    <p className="text-muted mb-0">{sold}</p>
                                                </td>
                                            </tr>
                                            <tr>
                                                <td>
                                                    <h5 className="font-size-14 mb-0">Lost</h5>
                                                </td>
                                                <td><div id="spak-chart3"></div></td>
                                                <td>
                                                    <p className="text-muted mb-0">{lost}</p>
                                                </td>
                                            </tr>
                                            <tr>
                                                <td>
                                                    <h5 className="font-size-14 mb-0">Completed</h5>
                                                </td>
                                                <td><div id="spak-chart3"></div></td>
                                                <td>
                                                    <p className="text-muted mb-0">{completed}</p>
                                                </td>
                                            </tr>
                                        </tbody>
                                    </Table>
                                </div>)}
                            </div>
                        </CardBody>
                    </Card>
                </Col>
            </React.Fragment>
        );
    }
}

export default Sources;
